$color-white: rgb(255, 255, 255);

$color-veryDarkBlue: rgb(20,28,48);
$color-bg-api: rgb(31,42,72);

$color-skyBlue: rgb(16,79,158);
$color-verySkyBlue: rgb(0,121,254);

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

#root {
  	width: 100vw;
	padding: 1rem 0;
	justify-content: center;
  	color: $color-white;
	display: flex;
	align-items: center;
}

button {
	color: $color-white;
	border: none;
	background: none;
}

svg {
	min-width: 20px;
	min-height: 20px;
	max-width: 20px;
	max-height: 20px;
}

.container-dad {
	width: 335px;
  	height: 400px;
	gap: 1rem;
  	flex-direction: column;
  	display: flex;

	.container-theme {
		width: 100%;
		grid-template-columns: 70% 20% 10%;
    	display: grid;
		align-items: center;

		h2 {
			font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
		}

		.btn-theme:hover {
			background-color: rgba(255, 255, 255, .1);
		}
  	}

	.search {
		width: 100%;
		padding: .5rem .7rem;
		grid-template-columns: 10% 65% 25%;
		gap: .1rem;
    	display: grid;
		border-radius: .5rem;
		background-color: $color-bg-api;
		align-items: center;

		label {
			svg {
				color: $color-verySkyBlue;
			}
		}

		input {
			padding: .5rem .2rem;
			outline: none;
			letter-spacing: .5px;
			font-size: 1rem;
			color: $color-white;
			border: none;
			background: none !important;
		
			&::placeholder {
				font-size: .85rem;
			}
		}

		.btn-search {
			padding: .9rem .7rem;
			font-family: monospace;
			border-radius: .7rem;
			background-color: $color-verySkyBlue;

			&:active {
				background-color: $color-skyBlue;
			}
		}
	}

	.container-data-api {
		justify-content: center;
		display: flex;
		background-color: $color-bg-api;

		.container-rows {
			width: 100%;
			padding: 2rem;
			gap: 2rem;
			flex-direction: column;
			display: flex;

			.row1, .row2, .info-user {
				display: flex;
			}

	
			.row1 {
				gap: 1rem;
				align-items: center;

				.logo {
					img {
						width: 80px;
						border-radius: 40px;
					}
				}
	
				.info-user {
					gap: .5rem;
					font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
					flex-direction: column;

					h2 {
						font-size: .9rem;
					}

					p:nth-child(2) {
						font-size: .8rem;
						color: $color-verySkyBlue;
					}
					p:nth-child(3) {
						font-size: .8rem;
						word-spacing: 3px;
					}
				}
			}
	
			.row2 {
				padding: 1rem;
				justify-content: space-around;
				border-radius: .5rem;
				background-color: $color-veryDarkBlue;

				.column {
					text-align: center;
					gap: .5rem;
					flex-direction: column;
					display: flex;

					.p-bold {
						font-weight: bold;
					}
				}
			}
	
			.row3 {
				grid-template-columns: 1fr;
				gap: 1rem;
				display: grid;

				.line {
					gap: .5rem;
					display: flex;
					align-items: center;

					a {
						text-decoration: none;
						color: $color-verySkyBlue;
					}
				}
			}
		}
	}
	
}